<script>
import dayjs from "dayjs";
import YearCalendar from "./YearCalendar.vue";

export default {
  name: "primary",
  components: {
    YearCalendar
  },
  data() {
    return {
      flag: "update",
      statusMsg: "",
      stateMsg: "",
      cp_id: "",
      // 日期保存时候用
      tog: "",
      // 头部变量
      form_name: "",
      concurrentPath: "",
      patientName: "",
      patientSex: "",
      patientAge: "",
      outpatient_id: "",
      hospital_id: "",
      admission_date: "",
      discharge_date: "",
      // main第一种病
      suitable_subject_disc: "",
      standard_entry_days: "",
      current_time: "",
      checked: "",
      primary_diagnosi: [],
      long_term_orders: [],
      temporary_orders: [],
      discharge_orders: [],
      notification: [],
      major_nursing_work: [],
      disease_variation_status: "1",
      reason: [],
      options: [
        {
          value: "白班",
          label: "白班"
        },
        {
          value: "小夜班",
          label: "小夜班"
        },
        {
          value: "大夜班",
          label: "大夜班"
        },
        {
          value: "无明确时间",
          label: "无明确时间"
        }
      ],
      nurse_onduty: "",
      day_shiftName: [],
      night_shiftSName: [],
      night_shiftBName: [],
      time_undefinedName: [],
      physician_name: [],
      lang: "ch", // 'en', 'ch', 'pt'
      year: 2019,
      activeDates: [],
      activeClass: "",
      hospitalizationYear: 0,
      hospitalizationMonth: 0,
      hospitalizationDay: 0,
      start: "",
      end: ""
    };
  },
  created() {
    // console.log(this.value);
    // if (this.concurrentPath != "") {
    //   this.form_name = this.form_name + "(" + this.concurrentPath + ")";
    // }
    this.getList();
  },
  methods: {
    action() {
      this.$axios
        .get("http://192.168.50.78:8001/getExamFormDataByStage", {
          params: {
            cp_id: this.cp_id
          }
        })
        .then(res => {
          // console.log(res);
          this.statusMsg = res.data.status;
          if (this.statusMsg == 404) {
            this.$message({
              showClose: true,
              message: "请输入正确的ID号查询",
              type: "error"
            });
            this.cp_id = "";
            this.form_name = "";
            this.patientName = "";
            this.patientSex = "";
            this.patientAge = "";
            this.outpatient_id = "";
            this.hospital_id = "";
            this.admission_date = "";
            this.discharge_date = "";
            this.standard_entry_days = "";
            this.suitable_subject_disc = "";
            this.current_time = "";
            this.primary_diagnosi = "";
            this.long_term_orders = "";
            this.temporary_orders = "";
            this.discharge_orders = "";
            this.notification = "";
            this.major_nursing_work = "";
            this.disease_variation_status = "";
            this.reason = "";
            this.physician_name = "";
            //  护士阶段
            this.day_shiftName = "";
            this.night_shiftBName = "";
            this.night_shiftSName = "";
            this.time_undefinedName = "";
            return;
          } else {
            this.form_name = res.data.form_name;
            /*  this.patientName = res.data.patient_info.name;
            this.patientSex = res.data.patient_info.sex;
            this.patientAge = res.data.patient_info.age; */
            this.outpatient_id = res.data.info_in_hospital.outpatient_id;
            this.hospital_id = res.data.info_in_hospital.hospital_id;
            // this.admission_date = res.data.info_in_hospital.admission_date;
            // this.discharge_date = res.data.info_in_hospital.discharge_date;
            this.standard_entry_days =
              res.data.info_in_hospital.standard_entry_days;
            this.suitable_subject_disc = res.data.suitable_subject_disc;
            this.current_time = res.data.treatment_info.stage.current_time;
            this.primary_diagnosi =
              res.data.treatment_info.stage.primary_diagnosi;
            this.long_term_orders =
              res.data.treatment_info.stage.key_orders.long_term_orders;
            this.temporary_orders =
              res.data.treatment_info.stage.key_orders.temporary_orders;
            this.discharge_orders =
              res.data.treatment_info.stage.key_orders.discharge_orders;
            this.notification =
              res.data.treatment_info.stage.key_orders.notification;
            // console.log(JSON.stringify(this.notification));
            this.major_nursing_work =
              res.data.treatment_info.stage.major_nursing_work;
            this.disease_variation_status =
              res.data.treatment_info.stage.disease_variation.status;
            this.reason =
              res.data.treatment_info.stage.disease_variation.reason;
            this.physician_name = res.data.treatment_info.stage.physician_name;
            //  护士阶段
            this.day_shiftName =
              res.data.treatment_info.stage.nurse_onduty.day_shift;
            this.night_shiftBName =
              res.data.treatment_info.stage.nurse_onduty.night_shift_b;
            this.night_shiftSName =
              res.data.treatment_info.stage.nurse_onduty.night_shift_s;
            this.time_undefinedName =
              res.data.treatment_info.stage.nurse_onduty.time_undefined;
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    btn() {
      const h = this.$createElement;
      let primary_diagnos = JSON.stringify(this.primary_diagnosi);
      let long_term_order = JSON.stringify(this.long_term_orders);
      let temporary_order = JSON.stringify(this.temporary_orders);
      let discharge_order = JSON.stringify(this.discharge_orders);
      let notificatio = JSON.stringify(this.notification);
      let major_nursing_wor = JSON.stringify(this.major_nursing_work);
      let disease_variation_statu = JSON.stringify(
        this.disease_variation_status
      );
      let reaso = JSON.stringify(this.reason);
      let nurse_ondut = JSON.stringify(this.nurse_onduty);
      let day_shiftNam = JSON.stringify(this.day_shiftName);
      let night_shiftSNma = JSON.stringify(this.night_shiftSName);
      let night_shiftBNma = JSON.stringify(this.night_shiftBName);
      let time_undefinedNma = JSON.stringify(this.time_undefinedName);
      let physician_nam = JSON.stringify(this.physician_name);
      this.$msgbox({
        title: "提示消息",
        message: h("p", null, [h("div", null, "是否确定更改数据 ")]),
        showCancelButton: true,
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        beforeClose: (action, instance, done) => {
          if (action === "confirm") {
            instance.confirmButtonLoading = true;
            instance.confirmButtonText = "保存中...";
            setTimeout(() => {
              this.$axios
                .get("http://192.168.50.78:8001/editExamFormDataById", {
                  params: {
                    flag: this.flag,
                    cp_id: this.cp_id,
                    dateInfo: this.tog,
                    current_time: this.current_time,
                    primary_diagnosi: primary_diagnos,
                    long_term_orders: long_term_order,
                    temporary_orders: temporary_order,
                    discharge_orders: discharge_order,
                    notification: notificatio,
                    major_nursing_work: major_nursing_wor,
                    disease_variation_status: disease_variation_statu,
                    reason: reaso,
                    nurse_onduty: nurse_ondut,
                    day_shiftName: day_shiftNam,
                    night_shiftSName: night_shiftSNma,
                    night_shiftBName: night_shiftBNma,
                    time_undefinedName: time_undefinedNma,
                    physician_name: physician_nam
                  }
                })
                .then(res => {
                  // console.log(res);
                  this.statusMsg = res.data.status;
                  if (this.statusMsg == 500) {
                    this.$message({
                      showClose: true,
                      message: "请先查询出有效信息再提交数据",
                      type: "error"
                    });
                  } else {
                    setTimeout(window.location.reload(), 5000);
                  }
                })
                .catch(error => {
                  console.log(error);
                  this.$message.error("哦豁~~数据保存失败啦~~");
                });

              setTimeout(() => {
                instance.confirmButtonLoading = false;
              }, 300);
            }, 3000);
          } else {
            done();
          }
        }
      }).catch(() => {
        this.$message("数据未保存~~");
      });
    },

    //封装axios方便多次引用
    getList() {
      this.$axios
        .get("http://192.168.50.78:8001/getExamFormData")
        .then(
          function(res) {
            // console.log(res)
            this.start = res.data.info_in_hospital.admission_date;
            this.end = res.data.info_in_hospital.discharge_date;
            this.hospitalizationYear = res.data.info_in_hospital.admission_date.split(
              "-"
            )[0];
            this.hospitalizationMonth = res.data.info_in_hospital.admission_date.split(
              "-"
            )[1];
            this.hospitalizationDay = res.data.info_in_hospital.admission_date.split(
              "-"
            )[2];
            for (
              let i = 0;
              i <
              dayjs(res.data.info_in_hospital.discharge_date).diff(
                dayjs(res.data.info_in_hospital.admission_date),
                "day"
              ) +
                1;
              i++
            ) {
              if (Number(this.hospitalizationMonth) > 12) {
                this.hospitalizationMonth = 1;
                this.hospitalizationYear++;
              }
              if (
                Number(this.hospitalizationDay) >
                dayjs(
                  this.hospitalizationYear +
                    "-" +
                    this.hospitalizationMonth +
                    "-" +
                    this.hospitalizationDay
                ).daysInMonth()
              ) {
                this.hospitalizationDay = 1;
                this.hospitalizationMonth++;
              }
              this.activeDates.push({
                date:
                  this.hospitalizationYear +
                  "-" +
                  this.hospitalizationMonth +
                  "-" +
                  this.hospitalizationDay,
                className: "gray"
              });
              this.hospitalizationDay++;
            }
            this.activeDates.push({
              date: dayjs().format("YYYY-MM-DD"),
              className: "blue"
            });
          }.bind(this)
        )
        .catch(function(error) {
          console.log(error);
        });
    },
    wiki() {
      window.open("http://127.0.0.1:5500/new_bdbk.html");
    },
    bpmn() {
      window.open("http://127.0.0.1:8081");
    },
    stand(isStandard) {
      this.activeDates = [];
      if (isStandard) {
        this.activeDates.push({
          date: dayjs().format("YYYY-MM-DD"),
          className: "blue"
        });
        this.start = "";
        this.end = "";
      } else {
        this.getList();
      }
    },
    personal(isPerson) {
      // console.log(isPerson);
    },
    toggleDate(dateInfo) {
      // console.log(dateInfo);
      this.tog = dateInfo;
      let cpId = this.cp_id;
      if (cpId == "") {
        this.$message({
          showClose: true,
          message: "请先输入ID号",
          type: "warning"
        });
      } else {
        this.$axios
          .get("http://192.168.50.78:8001/getExamFormDataByStage", {
            params: {
              cp_id: this.cp_id,
              dateInfo: dateInfo
            }
          })
          .then(res => {
            // console.log(res);
            this.stateMsg = res.data.state;
            this.statusMsg = res.data.status;
            if (this.stateMsg == 500 || this.statusMsg == 404) {
              this.$message({
                showClose: true,
                message: "该日期不存在住院信息"
              });
              this.current_time = "";
              this.primary_diagnosi = "";
              this.long_term_orders = "";
              this.temporary_orders = "";
              this.discharge_orders = "";
              this.notification = "";
              // console.log(JSON.stringify(this.notification));
              this.major_nursing_work = "";
              this.disease_variation_status = "";
              this.reason = "";
              this.physician_name = "";
              //  护士阶段
              this.day_shiftName = "";
              this.night_shiftBName = "";
              this.night_shiftSName = "";
              this.time_undefinedName = "";
            } else {
              this.current_time = res.data.treatment_info.stage.current_time;
              this.primary_diagnosi =
                res.data.treatment_info.stage.primary_diagnosi;
              this.long_term_orders =
                res.data.treatment_info.stage.key_orders.long_term_orders;
              this.temporary_orders =
                res.data.treatment_info.stage.key_orders.temporary_orders;
              this.discharge_orders =
                res.data.treatment_info.stage.key_orders.discharge_orders;
              this.notification =
                res.data.treatment_info.stage.key_orders.notification;
              // console.log(JSON.stringify(this.notification));
              this.major_nursing_work =
                res.data.treatment_info.stage.major_nursing_work;
              this.disease_variation_status =
                res.data.treatment_info.stage.disease_variation.status;
              this.reason =
                res.data.treatment_info.stage.disease_variation.reason;
              this.physician_name =
                res.data.treatment_info.stage.physician_name;
              //  护士阶段
              this.day_shiftName =
                res.data.treatment_info.stage.nurse_onduty.day_shift;
              this.night_shiftBName =
                res.data.treatment_info.stage.nurse_onduty.night_shift_b;
              this.night_shiftSName =
                res.data.treatment_info.stage.nurse_onduty.night_shift_s;
              this.time_undefinedName =
                res.data.treatment_info.stage.nurse_onduty.time_undefined;
            }
          })
          .catch(err => {
            console.log(err);
          });
      }
    },
    add_sat_and_sun_of_year() {
      let theDate = dayjs(`${this.year}-01-01`);

      while (theDate.diff(theDate.endOf("year"), "day") !== 0) {
        if (theDate.day() === 6 || theDate.day() === 0) {
          // 將週六或週日加入 activeDates 中
          // add weekend to activeDates
          this.activeDates.push(theDate.format("YYYY-MM-DD"));
        }
        theDate = theDate.add(1, "day");
      }

      // remove duplicate key
      this.activeDates = this.activeDates
        .filter(function(item, pos, self) {
          return self.indexOf(item) === pos;
        })
        .sort();
    },
    remove_sat_and_sun_of_year() {
      for (let i = this.activeDates.length - 1; i >= 0; i--) {
        let date = this.activeDates[i];
        if (
          dayjs(date).year() === this.year &&
          (dayjs(date).day() === 6 || dayjs(date).day() === 0)
        ) {
          this.activeDates.splice(i, 1);
        }
      }
    }
  }
};
</script>

